:root {
  --main-ff: "Inter", sans-serif;
  --second-ff: "RobotoMono", sans-serif;
  --color-dark: #000;
  --color-white: #fff;
  --color-btn-bg: linear-gradient(91.83deg, #095092 0%, #00a7e5 100%);
  --color-light-blue: #00a7e5;
  --color-blue: #0578B9;
  --color-card-grey: #CDCDD1;
  --color-bg: #DDDDE2;
  --border-color: #E1E1E1;
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Regular.woff2") format("woff2"), url("../fonts/RobotoMono-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Medium.woff2") format("woff2"), url("../fonts/RobotoMono-Medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Bold.woff2") format("woff2"), url("../fonts/RobotoMono-Bold.woff") format("woff");
}

.button-reset {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  border: none;
  font-family: var(--main-ff);
}

.logo {
  width: 20rem;
}

@media screen and (max-width: 767px) {
  .logo {
    width: 14.3rem;
  }
}

.nav ul {
  display: flex;
  align-items: center;
}

.nav ul li {
  margin-right: 2.5rem;
}

.nav ul li:last-child {
  margin-right: 0;
}

.nav ul li a {
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
}

.footer-nav ul li {
  margin-bottom: 4.5rem;
}

@media screen and (max-width: 767px) {
  .footer-nav ul li {
    margin-bottom: 3rem;
  }
}

.footer-nav ul li:last-child {
  margin-bottom: 0;
}

.footer-nav ul li a {
  font-weight: 500;
  font-size: 2.2rem;
  line-height: 3rem;
  font-family: var(--second-ff);
}

@media screen and (max-width: 767px) {
  .footer-nav ul li a {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}

@media screen and (max-width: 991px) {
  .footer-nav ul {
    display: flex;
    flex-wrap: wrap;
  }
  .footer-nav ul li {
    width: 50%;
    margin-bottom: 3rem;
  }
}

.second-font {
  font-weight: 500;
  font-family: var(--second-ff);
}

.second-title {
  font-family: var(--second-ff);
  font-size: 4rem;
  line-height: 4.5rem;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 24rem;
  background: var(--color-btn-bg);
  color: var(--color-white);
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  border-radius: 1rem;
  cursor: pointer;
  overflow: hidden;
}

.button span {
  display: flex;
  align-items: center;
  color: transparent;
  position: relative;
  line-height: 1;
  padding: 1.9rem 0 1.9rem 1.5rem;
  flex-shrink: 0;
}

.button span::before {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  content: attr(data-text);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  position: absolute;
  left: 1rem;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all .3s ease;
  transform: translateY(0) translateZ(0);
}

.button span::after {
  top: 100%;
  opacity: 0;
}

.button__icon {
  fill: var(--color-white);
  margin-right: 1.5rem;
  transition: transform 200ms;
}

.button_white {
  background: var(--color-white);
}

.button_white span::before, .button_white span::after {
  color: var(--color-blue);
}

.button_white .button__icon {
  fill: var(--color-blue);
}

.button_white-black {
  background: var(--color-white);
}

.button_white-black span::before, .button_white-black span::after {
  color: var(--color-dark);
}

.button_white-black .button__icon {
  fill: var(--color-dark);
}

.button:hover span::before {
  opacity: 0;
}

.button:hover span::after {
  opacity: 1;
}

.button:hover span::before, .button:hover span::after {
  transform: translateY(-100%) translateZ(0);
}

.button:hover .button__icon-plus {
  transform: rotate(-90deg);
}

.form {
  width: 100%;
}

.form__heading {
  display: block;
  margin-bottom: 3rem;
  font-size: 2rem;
  line-height: 4.5rem;
}

.form__input-container {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 2rem;
}

.form__input-container ul {
  position: absolute;
  bottom: 0.2rem;
  right: 0.5rem;
  font-weight: 500;
  font-size: 1.2rem;
  color: red;
}

.form__input-container._mb10 {
  margin-bottom: 1rem;
}

.form__input {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  outline: none;
}

.form__input::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__input.parsley-error {
  border-color: red !important;
}

.form__textarea {
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  padding: 1.7rem 1.7rem 1.8rem;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  min-height: 10.6rem;
  resize: none;
  outline: none;
}

.form__textarea::placeholder {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  color: var(--color-dark);
}

.form__button-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__button-wrapper p {
  max-width: 15.5rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: rgba(111, 111, 111, 0.7);
}

.form__button-wrapper p a {
  text-decoration: underline;
}

.form__button-wrapper p a:hover {
  text-decoration: none;
}

.form__button {
  font-weight: 600;
  min-width: 14.9rem;
}

.form__button._width {
  min-width: 17.2rem;
}

.form._white .form__input {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__input::placeholder {
  color: var(--color-white);
}

.form._white .form__textarea {
  border-color: var(--color-white);
  color: var(--color-white);
}

.form._white .form__textarea::placeholder {
  color: var(--color-white);
}

.form._white .form__button-wrapper p {
  color: var(--color-white);
  max-width: 17.5rem;
}

.form._white .form__button {
  background: var(--color-white);
  color: var(--color-dark);
}

.form._white .form__button span::before, .form._white .form__button span::after {
  color: var(--color-dark);
}

.form._white .form__button-icon {
  fill: var(--color-dark);
}

.social {
  display: flex;
  align-items: center;
}

.social__item {
  margin-right: 1.5rem;
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  fill: var(--color-dark);
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: var(--color-bg);
  transform: opacity 200ms, visibillty 200ms;
}

.preloader._is-loaded {
  opacity: 0;
  visibility: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

