.cards {
  position: relative;
  z-index: 5;
  width: 100%;
  counter-reset: section;
}

.cards__item {
  position: relative;
  z-index: 4;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  background-color: var(--color-white);
  padding: 3rem 2.7rem 1.5rem 2.7rem;
  border-radius: 2rem;
  align-items: flex-end;
}

@media screen and (max-width: 991px) {
  .cards__item {
    width: 100%;
    max-width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .cards__item {
    padding: 2.5rem 1.5rem 1.2rem 1.5rem;
  }
}

.cards__item:not(:first-child) {
  position: absolute;
}

.cards__item:nth-child(2) {
  top: 4rem;
  left: 0;
  z-index: 3;
}

.cards__item:nth-child(3) {
  top: 8rem;
  z-index: 2;
}

.cards__item:nth-child(4) {
  top: 12rem;
  z-index: 1;
}

.cards__item:nth-child(5) {
  top: 16rem;
  z-index: 0;
}

.cards__item:nth-child(6) {
  top: 20rem;
  z-index: -1;
}

.cards__item:nth-child(7) {
  top: 24rem;
  z-index: -2;
}

.cards__item:nth-child(2n) {
