}

.cards__item::before {
  position: absolute;
  left: 2.7rem;
  bottom: 1.6rem;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.5rem;
  counter-increment: section;
  content: counter(section);
}

