  }
}

.steps__card-wrapper {
  position: relative;
}

@media screen and (max-width: 991px) {
  .steps__card-wrapper {
    align-self: flex-start;
  }
}

.customers {
  margin-bottom: 20.2rem;
}

@media screen and (max-width: 767px) {
  .customers {
    margin-bottom: 13.5rem;
  }
}

.customers__heading-wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
  margin-bottom: 7.1rem;
}

@media screen and (max-width: 991px) {
  .customers__heading-wrapper {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .customers__heading-wrapper {
    margin-bottom: 5.5rem;
  }
}

.customers__left-block {
  grid-column: 6 span;
}

@media screen and (max-width: 991px) {
  .customers__left-block {
    margin-bottom: 3.2rem;
  }
}

.customers__right-block {
  grid-column: 6 span;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .customers__right-block {
    flex-direction: column;
  }
}

.customers__content h2 {
  font-size: 4rem;
  line-height: 4.5rem;
  max-width: 46rem;
}

@media screen and (max-width: 767px) {
  .customers__content h2 {
    font-size: 2rem;
    line-height: 3rem;
  }
}

.customers__count-block {
  display: flex;
}

@media screen and (max-width: 767px) {
  .customers__count-block {
    margin-bottom: 5.5rem;
  }
}

.customers__count-block-item {
  margin-right: 6.2rem;
}

.customers__count-block-item:last-child {
  margin-right: 0;
}

.customers__count-block-item span {
  position: relative;
  display: block;
  font-weight: 400;
  font-size: 7.2rem;
  line-height: 1;
  font-family: var(--second-ff);
  margin-bottom: 1rem;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.customers__count-block-item span sup {
  display: block;
  position: absolute;
  top: 1rem;
  right: 1.4rem;
  font-size: 2.5rem;
  line-height: 1;
  background: var(--color-btn-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.customers__count-block-item p {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 1.7rem;
  font-family: var(--second-ff);
  max-width: 12.5rem;
}

.customers__btn {
  min-width: 22.1rem;
}
