.header {
	padding-top: 3rem;

	@include mq(sm) {
		padding-top: 2rem;
	}

	&__container {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 20px;
	}

	&_left {
		grid-column: 6 span;
		display: flex;
		align-items: center;
	}

	&__logo {
		flex-shrink: 0;
	}

	&_right {
		grid-column: 6 span;
		display: flex;
		align-items: center;
	}

	&__button {
		display: inline-block;
		margin-left: auto;
		background: url(../img/white-noise.png), var(--color-btn-bg);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		color: var(--color-white);
		font-weight: 600;
		font-size: 1.4rem;
		line-height: 1.7rem;
		padding: 0.6rem 1rem;
		border-radius: 2rem;
		cursor: pointer;
	}

	&__nav {
		@include mq(lg) {
			display: none;
		}
	}

	&__mobile-btn {
		display: none;
		align-items: center;
		justify-content: center;
		width: 4rem;
		height: 4rem;
		margin-right: 2rem;

		&-icon {
			width: 2rem;
			height: 2rem;
		}

		@include mq(lg) {
			display: flex;
		}
	}
}
